        font-size: 14px;
        line-height: 1.3;
        border: none;
        color: #2f343b;
    }
    .pptwj .pptwj-tabs-wrap .boxes ul.tab-filter-list {
        background: #fff;
        margin: -15px 0 15px 0;
        border-bottom: 1px solid #e0e0e0;
    }
    .pptwj .pptwj-tabs-wrap .boxes ul.tab-filter-list li {
        padding: 0;
    }
    .pptwj .pptwj-tabs-wrap .boxes ul.tab-filter-list li a {
        display: inline-block;
        padding: 8px 2px 5px 1px;
        position: relative;
        font-family: 'Lato', sans-serif;
        border: none;
        color: #9a9a90;
    }
    .pptwj .pptwj-tabs-wrap .boxes ul.tab-filter-list li a.selected,
    .pptwj .pptwj-tabs-wrap .boxes ul.tab-filter-list li a:hover {
        color: #9370DB;
    }
    .pptwj .pptwj-tabs-wrap .boxes ul.tab-filter-list li a.selected::after,
    .pptwj .pptwj-tabs-wrap .boxes ul.tab-filter-list li a:hover::after {
        position: absolute;
        content: '';
        width: 100%;
        height: 2px;
        left: 0;
        bottom: 0;
        background-color: #9370DB;
    }

    .gallery-caption,
    .bypostauthor {
        font-weight: normal;
    }

    .woocommerce-custom {
        margin-top: 30px;
    }

    .woocommerce {
        font-family: 'Open Sans', sans-serif;
    }
    .woocommerce .woocommerce-custom a,
    .woocommerce .sidebar a {
        border: 0;
    }
    .woocommerce .myaccount_address {
        margin-bottom: 30px;
    }

    .woocommerce nav.woocommerce-pagination ul,
    .woocommerce nav.woocommerce-pagination ul li,
    .woocommerce-page nav.woocommerce-pagination ul li,
    .woocommerce-page nav.woocommerce-pagination ul {
        border: none;
        margin: 1px;
    }

    .error {
        margin: 15px 0 30px 0;
        padding: 10px;
    }
    @media (min-width: 768px) {
        .error {
            margin: 30px 0 60px 0;
        }
    }
    .error span {
        display: inline-block;
        font-family: 'Open Sans', sans-serif;
        font-weight: bold;
        color: #f3f4f4;
        font-size: 120px;
        text-shadow: 0 0 1px #b4b4b4;
        line-height: 1.3;
    }
    @media (min-width: 768px) {
        .error span {
            font-size: 200px;
        }
    }
    .error .primary-color {
        color: #9370DB;
    }
    .error .transform {
        -webkit-transform: rotate(180deg);
        -ms-transform: rotate(180deg);
        transform: rotate(180deg);
    }
    .error p {
        font-size: 20px;
    }

    .error__hr {
        width: 60%;
        margin: 30px auto;
    }

    /* Tags para imagens centeralizadas */
    img.alignright {
        float: right;
        margin: 0 0 1em 1em;
    }
    img.alignleft {
        float: left;
        margin: 0 1em 1em 0;
    }
    img.aligncenter {
        display: block;
        margin-left: auto;
        margin-right: auto;
    }
    .alignright {
        float: right;
    }
    .alignleft {
        float: left;
    }
    .aligncenter {
        display: block;
        margin-left: auto;
        margin-right: auto;
    }




