.piada code {
  padding: 5px;
  display: inline-block;
  margin-bottom: 20px;
  border: dotted;
  border-width: 1px; }

.piada .foto {
  display: inline-block; }

.piada .center {
  text-align: center; }

.piada .flip {
  -webkit-transform: scaleX(-1);
  -ms-transform: scaleX(-1);
      transform: scaleX(-1);
  -webkit-filter: FlipH;
          filter: FlipH;
  -ms-filter: 'FlipH'; }

.piada blockquote {
  display: inline-block;
  font-family: Georgia, serif;
  font-size: 18px;
  font-style: italic;
  margin: 0.25em 0;
  padding: 0.25em 40px;
  line-height: 1.45;
  position: relative;
  margin-top: 20px;
  border: 0px;
  margin-bottom: 20px; }

.piada blockquote:before {
  display: inline-block;
  content: '\201C';
  font-size: 80px;
  position: absolute;
  left: -20px;
  top: -20px;
  color: #7a7a7a; }

.piada .wrapper {
  display: block;
  position: absolute;
  left: 50%;
  top: 50%;
  height: 300px; }

.piada .p-loader {
  display: inline-block;
  width: 30px; }



