html {
  background-color: #f3f4f4; }

.blog-post {
  margin: 20px 5px; }
  .blog-post span {
    margin-right: 5px; }
  .blog-post h1 {
    display: inline-block;
    font-weight: bold;
    font-size: 20px; }
  .blog-post .post-title {
    font-size: 50px; }
  .blog-post .description {
    margin-left: 15px; }
  .blog-post .anchor {
    display: inline;
    font-size: 20px;
    font-weight: normal; }



