@import url("blog-theme.scss");
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif; }

.social-button a {
  display: block;
  color: black;
  font-size: 20px;
  margin: auto 5px;
  width: 30px; }
  @media (min-width: 768px) {
    .social-button a {
      color: white; } }
  .social-button a:hover {
    color: #9370DB;
    -webkit-transition: color 400ms linear;
    -o-transition: color 400ms linear;
    transition: color 400ms linear; }



